body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#chatbot-container {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 300px;
  height: 400px;
  background-color: #f1f1f1;
  border-radius: 5px 0 0 0;
  z-index: 9999;
}

#chatbot-messages {
  height: 330px;
  overflow-y: scroll;
  padding: 10px;
}

#chatbot-input {
  width: 70%;
  height: 40px;
  border-radius: 20px;
  border: none;
  padding: 10px;
  margin-right: 10px;
  font-size: 16px;
}

button {
  width: 20%;
  height: 40px;
  border-radius: 20px;
  border: none;
  background-color: #4CAF50;
  color: white;
  font-size: 16px;
}

button:hover {
  background-color: #3e8e41;
}

.message-container {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.message-text {
  background-color: #fff;
  padding: 10px;
  border-radius: 20px;
  box-shadow: 0px 0px 5px #888;
  margin-right: 10px;
}